import React, { useState, useEffect } from 'react';
import './css/Footer.css';

function Footer() {
  const [animationOverd, setAnimationOverd] = useState(0);

  const images = [
    "footer-city2-1.png",
    "footer-city2-2.png",
    "footer-city2-3.png",
    "footer-city2-4.png",
    "footer-city2-5.png",
    "footer-city2-6.png",
    "footer-city2-7.png"
  ];

  const imageItems = images.map((item,index) => 
    <>
      <img key={item.toString()} src={`${process.env.PUBLIC_URL}/img/${item}`} alt="" className={animationOverd >= index + 1 ? "footer-image active" : "footer-image"} />
    </>
  );

  const footerEvent = () => {
    const promise = new Promise((resolve) => {
      setTimeout(() => {
        resolve(setAnimationOverd(1));
      },250)
    })
    promise.then((resolve) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(setAnimationOverd(2));
        },250)
      })
    }).then((resolve) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(setAnimationOverd(3));
        },250)
      })
    }).then((resolve) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(setAnimationOverd(4));
        },250)
      })
    }).then((resolve) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(setAnimationOverd(5));
        },250)
      })
    }).then((resolve) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(setAnimationOverd(6));
        },250)
      })
    }).then((resolve) => {
        setTimeout(() => {
          setAnimationOverd(7);
        },250)
    })
  }

  const callFooterEvent = () => {
    if(animationOverd === 0){
      footerEvent();
    }
  }

  return(
    <>
      <div className="footer" onLoad={callFooterEvent()}>
        <div className="footer-images">
          {imageItems}
        </div>
        <div className="footer-text">
          <h1>info</h1>
        </div>
      </div>
    </>
  )
}

export default Footer;